@import url(https://fonts.googleapis.com/css?family=Lexend+Mega&display=swap);
.img-left-col {
  background-image: url(/static/media/left-col.e1329fa5.jpg);
  background-position: center;
  background-size: cover;
  max-height: 100%;
}

.landing {
  height: 100%;
}

.greeting {
  border-bottom: solid 5px #212529;
}

.App {
  height: 100vh;
  width: 100vw;
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
}

#accordion {
  max-height: 100vh!important;
  overflow-y: auto;
}

.element::-webkit-scrollbar {
  width: 0 !important;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
}

.img-container {
  max-height: 300px;
}

.icon {
  max-height: 50px;
}

#test {
  -webkit-transition: all ease-in-out 1s;
  transition: all ease-in-out 1s;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}


.text-container {
  background-color: #f0f0f0;
  border: none !important;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu,
    roboto, noto, segoe ui, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f2f2f2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
